import {util} from '@tensorflow/tfjs-core';

import {getCoordsDataType} from '../shader_preprocessor';
import {computeDispatch, flatDispatchLayout} from '../webgpu_util';

import {WebGPUProgram} from './webgpu_program';

export class MirrorPadProgram implements WebGPUProgram {
  outputShape: number[];
  shaderKey: string;
  dispatchLayout: {x: number[]};
  dispatch: [number, number, number];
  variableNames = ['x'];
  workGroupSize: [number, number, number] = [64, 1, 1];
  xShape: number[];
  paddings: Array<[number, number]>;
  offset: number;
  size: number;

  constructor(
      xShape: number[], paddings: Array<[number, number]>,
      mode: 'reflect'|'symmetric') {
    this.outputShape = paddings.map(
        (p, i) => p[0] /* beforePad */ + xShape[i] + p[1] /* afterPad */);
    this.dispatchLayout = flatDispatchLayout(this.outputShape);
    this.dispatch = computeDispatch(
        this.dispatchLayout, this.outputShape, this.workGroupSize);

    this.xShape = xShape;
    this.paddings = paddings;
    this.offset = mode === 'reflect' ? 0 : 1;
    this.shaderKey = `mirrorPad_${mode}_${paddings}_${xShape}`;
    this.size = util.sizeFromShape(this.outputShape);
  }

  getUserCode(): string {
    const rank = this.xShape.length;
    const dtype = getCoordsDataType(rank);
    const start = this.paddings.map(p => p[0]).join(',');
    const end = this.paddings.map((p, i) => p[0] + this.xShape[i]).join(',');
    const unpackedCoords = rank > 1 ?
        ['coords[0]', 'coords[1]', 'coords[2]', 'coords[3]'].slice(0, rank) :
        'coords';


    if (rank === 1) {
      return `
        int start = ${start};
        int end = ${end};

        void main() {
          int outC = getOutputCoords();
          int index = int(gl_GlobalInvocationID.x);
          if (index < size)
          {
            if (outC < start) {
              outC = start * 2 - outC - ${this.offset};
            } else if(outC >= end) {
              outC = (end - 1) * 2 - outC + ${this.offset};
            }
            setOutput(index, getX(outC - start));
          }
        }
      `;
    } else {
      return `
      ${dtype} start = ${dtype}(${start});
      ${dtype} end = ${dtype}(${end});

      void main() {
        ${dtype} outC = getOutputCoords();
        int index = int(gl_GlobalInvocationID.x);
        if (index < size)
        {
          for (int i = 0; i < ${rank}; i++) {
            if (outC[i] < start[i]) {
              outC[i] = start[i] * 2 - outC[i] - ${this.offset};
            } else if(outC[i] >= end[i]) {
              outC[i] = (end[i] - 1) * 2 - outC[i] + ${this.offset};
            }
          }
          ${dtype} coords = outC - start;
          setOutput(index, getX(${unpackedCoords}));
        }
      }
    `;
    }
  }
}